import React from 'react';
import { Box, Grid, Paper, Typography } from '@mui/material';
import StorageIcon from '@mui/icons-material/Storage';
import GroupIcon from '@mui/icons-material/Group';
import ListAltIcon from '@mui/icons-material/ListAlt';

const stats = [
  { label: 'Total Asset Count', value: 7, icon: <StorageIcon fontSize="large" sx={{ color: '#4fd1c5' }} /> },
  { label: 'Total Account Count', value: 9, icon: <GroupIcon fontSize="large" sx={{ color: '#63b3ed' }} /> },
  { label: 'Total Audit Log Count', value: 174, icon: <ListAltIcon fontSize="large" sx={{ color: '#f6ad55' }} /> },
];

const Dashboard = () => (
  <Box sx={{ flexGrow: 1, p: 3 }}>
    <Grid container spacing={3}>
      {stats.map((stat) => (
        <Grid item xs={12} sm={6} md={4} key={stat.label}>
          <Paper sx={{ p: 3, background: '#262a3b', color: '#fff', display: 'flex', alignItems: 'center', gap: 2 }}>
            {stat.icon}
            <Box>
              <Typography variant="h5">{stat.value}</Typography>
              <Typography variant="body2">{stat.label}</Typography>
            </Box>
          </Paper>
        </Grid>
      ))}
    </Grid>
  </Box>
);

export default Dashboard;