import React, { useState } from 'react';
import { Box, Paper, Typography, Button, Snackbar, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, TextField, Dialog, DialogTitle, DialogContent, DialogActions } from '@mui/material';
import SshTerminal from '../components/SshTerminal';

export default function Assets() {
  const [assets, setAssets] = useState([
    { name: 'Ubuntu Server', ip: '192.168.2.208', os: 'Linux', username: 'ubuntu', password: '123456', port: 22 },
  ]);
  const [snackbar, setSnackbar] = useState({ open: false, message: '' });
  const [open, setOpen] = useState(false);
  const [newAsset, setNewAsset] = useState({ name: '', ip: '', os: '', username: '', password: '', port: 22 });
  const [selectedAsset, setSelectedAsset] = useState(null);

  function handleAddAsset() {
    setAssets([...assets, newAsset]);
    setNewAsset({ name: '', ip: '', os: '', username: '', password: '', port: 22 });
    setOpen(false);
  }

  return (
    <Box sx={{ flexGrow: 1, p: 3 }}>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
        <Typography variant="h5" sx={{ color: '#fff' }}>Assets</Typography>
        <Button variant="contained" sx={{ background: '#7c3aed' }} onClick={() => setOpen(true)}>
          + CREATE NEW
        </Button>
      </Box>
      <TableContainer component={Paper} sx={{ background: '#262a3b' }}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell sx={{ color: '#fff' }}>Name</TableCell>
              <TableCell sx={{ color: '#fff' }}>IP</TableCell>
              <TableCell sx={{ color: '#fff' }}>OS</TableCell>
              <TableCell sx={{ color: '#fff' }}>Username</TableCell>
              <TableCell sx={{ color: '#fff' }}>Port</TableCell>
              <TableCell sx={{ color: '#fff' }}>Actions</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {assets.map((asset, idx) => (
              <TableRow key={idx}>
                <TableCell sx={{ color: '#fff' }}>{asset.name}</TableCell>
                <TableCell sx={{ color: '#fff' }}>{asset.ip}</TableCell>
                <TableCell sx={{ color: '#fff' }}>{asset.os}</TableCell>
                <TableCell sx={{ color: '#fff' }}>{asset.username}</TableCell>
                <TableCell sx={{ color: '#fff' }}>{asset.port}</TableCell>
                <TableCell>
                  <Button
                    variant="contained"
                    onClick={() => setSelectedAsset(asset)}
                  >
                    Balan
                  </Button>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      <Snackbar
        open={snackbar.open}
        autoHideDuration={4000}
        onClose={() => setSnackbar({ open: false, message: '' })}
        message={snackbar.message}
      />

      {/* Sunucu Ekleme Dialogu */}
      <Dialog open={open} onClose={() => setOpen(false)}>
        <DialogTitle>Yeni Sunucu Ekle</DialogTitle>
        <DialogContent sx={{ display: 'flex', flexDirection: 'column', gap: 2, mt: 1 }}>
          <TextField label="Name" value={newAsset.name} onChange={e => setNewAsset({ ...newAsset, name: e.target.value })} />
          <TextField label="IP" value={newAsset.ip} onChange={e => setNewAsset({ ...newAsset, ip: e.target.value })} />
          <TextField label="OS" value={newAsset.os} onChange={e => setNewAsset({ ...newAsset, os: e.target.value })} />
          <TextField label="Username" value={newAsset.username} onChange={e => setNewAsset({ ...newAsset, username: e.target.value })} />
          <TextField label="Password" type="password" value={newAsset.password} onChange={e => setNewAsset({ ...newAsset, password: e.target.value })} />
          <TextField label="Port" type="number" value={newAsset.port} onChange={e => setNewAsset({ ...newAsset, port: Number(e.target.value) })} />
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setOpen(false)}>ptal</Button>
          <Button onClick={handleAddAsset} variant="contained">Ekle</Button>
        </DialogActions>
      </Dialog>

      {/* SSH Terminal Dialogu */}
      {selectedAsset && (
        <Dialog
          open={true}
          onClose={() => setSelectedAsset(null)}
          maxWidth="md"
          fullWidth
        >
          <DialogTitle>SSH Terminal - {selectedAsset.name}</DialogTitle>
          <DialogContent>
            <SshTerminal
              wsUrl="ws://localhost:8080/ws/ssh"
              sshInfo={{
                ip: selectedAsset.ip,
                port: selectedAsset.port,
                username: selectedAsset.username,
                password: selectedAsset.password
              }}
            />
          </DialogContent>
          <DialogActions>
            <Button onClick={() => setSelectedAsset(null)}>Kapat</Button>
          </DialogActions>
        </Dialog>
      )}
    </Box>
  );
}