import React from 'react';
import { Drawer, List, ListItemButton, ListItemIcon, ListItemText, Toolbar } from '@mui/material';
import DashboardIcon from '@mui/icons-material/Dashboard';
import StorageIcon from '@mui/icons-material/Storage';
import GroupIcon from '@mui/icons-material/Group';
import PolicyIcon from '@mui/icons-material/Policy';
import { Link, useLocation } from 'react-router-dom';

const menuItems = [
  { text: 'Dashboards', icon: <DashboardIcon />, path: '/' },
  { text: 'Assets', icon: <StorageIcon />, path: '/assets' },
  { text: 'Accounts', icon: <GroupIcon />, path: '/accounts' },
  { text: 'Policies', icon: <PolicyIcon />, path: '/policies' },
];

const Sidebar = () => {
  const location = useLocation();
  return (
    <Drawer
      variant="permanent"
      sx={{
        width: 220,
        flexShrink: 0,
        '& .MuiDrawer-paper': {
          width: 220,
          boxSizing: 'border-box',
          background: '#23263a',
          color: '#fff',
        },
      }}
    >
      <Toolbar />
      <List>
        {menuItems.map((item) => (
          <ListItemButton
            key={item.text}
            component={Link}
            to={item.path}
            selected={location.pathname === item.path}
            sx={{
              background: location.pathname === item.path ? '#282c3f' : 'inherit',
              color: location.pathname === item.path ? '#7c3aed' : '#fff',
            }}
          >
            <ListItemIcon sx={{ color: location.pathname === item.path ? '#7c3aed' : '#8f93b2' }}>
              {item.icon}
            </ListItemIcon>
            <ListItemText primary={item.text} />
          </ListItemButton>
        ))}
      </List>
    </Drawer>
  );
};

export default Sidebar;